@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Merriweather:wght@300;400;700&family=Montserrat:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0D001A;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{display:flex;justify-content:center;flex-direction:column;align-items:center;overflow:hidden}.App .container{width:60vw;display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));grid-template-rows:repeat(5, 1fr);grid-gap:2rem;gap:2rem;padding:3rem;font-family:'Josefin Sans', sans-serif;color:#0D001A;align-items:center;justify-content:center;text-align:center}.App .box{padding:1rem;display:flex;flex-direction:column;border-radius:5px;box-shadow:10px 10px 30px rgba(0,0,0,0.1);align-items:center;text-align:center;background-color:white}.App .searchBar{background-image:url(/eventfinder/static/media/background.61dd1885.png);background-repeat:no-repeat;height:400px;background-position:center;background-size:cover;align-items:center;width:100vw;padding:50px;text-align:center;font-family:'Merriweather', serif}.App .searchBar h1{font-size:50px;font-weight:700;letter-spacing:6px;padding:10px;color:#0D001A}.App .searchBar input{height:45px;font-size:20px;width:300px;font-family:'Montserrat', sans-serif;border-radius:100px}.App .searchBar button{width:7rem;height:50px;margin:10px;font-size:20px;border:none;background-color:#0D001A;cursor:pointer;border-radius:100px;color:white;font-family:'Montserrat', sans-serif}@media (max-width: 440px){.App .searchBar button{background:#8d6fac;color:white}}.App .searchBar button:hover{border:solid 2px #0D001A;color:#0D001A;background:none}@media (max-width: 440px){.App .searchBar button:hover{background:white;color:#0D001A}}

